import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ViewController, ToastController } from 'ionic-angular';
import { PostComment, Collections } from '../../app/app.models';
import { AngularFirestore } from 'angularfire2/firestore';
import { AuthProvider } from '../../providers/auth/auth';

@IonicPage()
@Component({
  selector: 'page-post-comments',
  templateUrl: 'post-comments.html',
})
export class PostCommentsPage {

  public comments: PostComment[] = this.navParams.get('comments')
  public post: string = this.navParams.get('post');
  public comment: Partial<PostComment> = {text: ''};

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public viewCtrl: ViewController,
    private afs: AngularFirestore,
    private auth: AuthProvider,
    private toast: ToastController,
  ) {
  }

  async addComment() {

    this.comment.id = this.afs.createId();
    this.comment.likes = [];
    this.comment.date = new Date().toISOString();
    this.comment.post = this.post;
    this.comment.user = this.auth.user;

    console.log(this.comment);

    await this.afs.collection(Collections.POST_COMMENT).doc(this.comment.id).set({ ...this.comment });
    this.toast.create({ message: 'Comentario agregado correctamente', duration: 2000 }).present();
    this.comments.push(this.comment as PostComment);

  }

}



